@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Righteous&display=swap);
body {
  font-family: 'Righteous', cursive;
  padding: 0;
  margin: 0;
  color: #d14f4f;
  line-height: 1.5;
  font-size: 1em;
  background-color: #222222;
  background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23424242' fill-opacity='0.4'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

/* navbar ---------- */

.navbar {
  background-color: rgba(22, 22, 22, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 0;
}

.link {
  color: #efefef;
  display: inline-block;
  padding: 3px 20px;
  font-size: 16px;
  text-decoration: none;
  transition: 0.3s ease all;
  margin-left: 1rem;
}

.link:hover {
  color: #f59494;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* nasa photo ---------- */

.nasa-photo {
  display: flex;
  padding: 40px;
  margin: 0 auto;
  max-height: 100%;
}

.nasa-photo img {
  width: 50%;
  max-width: 100%;
  height: auto;
  margin-right: 50px;
  object-fit: contain;
  max-height: 70vh;
}

.nasa-photo h1 {
  margin: 0;
  font-size: calc(16px + 2vw);
}

.nasa-photo .date {
  margin: 0 0 30px 0;
}

.nasa-photo .explanation {
  color: #bbb;
}

@media (max-width: 1280px) {
  .nasa-photo {
    display: block;
  }

  .nasa-photo img {
    width: 100%;
    margin-right: 0;
    margin-bottom: 30px;
  }
}

.home-link {
  font-size: 40px;
  background: #f59494;
  display: inline-block;
  padding: 40px 80px;
  color: #fff;
  text-decoration: none;
  transition: 0.3s ease all;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.home-link:hover {
  border-radius: 5px;
  background: #e77171;
}

.read-more{
  text-decoration: underline;
  cursor: pointer;
  transition: all 0.4s ease;
  color: #d14f4f;
}
.read-more:hover{
  color: #d14f4f95;
}
.d-none{
  display: none;
}
